import { pathParams } from "path-params";

export function maskPhoneNumber(phone = "") {
  const phoneNumber = phone.trim();

  if (phoneNumber === "+" || phoneNumber === "+7" || phoneNumber === "+7 (") {
    return phoneNumber;
  }

  if (phoneNumber === "7" || phoneNumber === "8") {
    return "+7";
  }

  const formattedPhone = phoneNumber.replace(/\D/g, "").slice(0, 12);

  const matchPhone = formattedPhone
    .replace(/\D/g, "")
    .replace(/^\+?([78])?/, "")
    .match(/^(\d{0,3})(\d{0,3})(\d{0,2})(\d{0,2})/);

  if (matchPhone) {
    if (matchPhone[1] && !matchPhone[2]) {
      return `+7 (${matchPhone[1]}`;
    }

    if (matchPhone[2]) {
      const number = matchPhone.slice(2, 5).filter(Boolean).join("-");

      return `+7 (${matchPhone[1]}) ${number}`;
    }

    return ``;
  }

  return phoneNumber;
}

export function maskSnils(snils = "") {
  const snilsNumber = snils.trim().replace(/\D/g, "");

  const matchSnils = snilsNumber.match(/^(\d{0,3})(\d{0,3})(\d{0,3})(\d{0,2})/);

  if (matchSnils) {
    const start = matchSnils.slice(1, 4).filter(Boolean).join("-");

    if (matchSnils[4]) {
      return `${start} ${matchSnils[4]}`;
    }

    return start;
  }

  return snilsNumber;
}

export function maskDivisionCode(value = "") {
  const code = value.trim();

  const matchCode = code.replace(/\D/g, "").match(/^(\d{0,3})(\d{0,3})$/);

  if (matchCode) {
    return [matchCode[1], matchCode[2] && "-", matchCode[2]]
      .filter(Boolean)
      .join("");
  }

  return "";
}

export function formatString(text = "", params = {}) {
  const matcher = text.match(/:[a-zA-Z]+/g);

  if (matcher) {
    const config = matcher.reduce((acc, item) => {
      const key = item.slice(1);

      const data = params[key];

      let value = "";

      if (data) {
        switch (data.type) {
          case "link":
            value = `<a target="_blank" rel="noopener noreferrer" class="app-link" href="${data.link}">${data.text}</a>`;
            break;
          default:
            value = "";
        }
      }

      return {
        ...acc,
        [key]: value,
      };
    }, {});

    return pathParams(text, config);
  }

  return text;
}
