import { getFormStatus } from "@/helpers/FormsHelpers";

export const resourcesModule = {
  namespaced: true,
  state: {
    usedExternalResources: false,
    usedInternalResources: false,
    externalResources: [],
    internalRussianResources: "",
    internalEnglishResources: "",
  },
  getters: {
    getUsedExternalResources(state) {
      return state.usedExternalResources;
    },
    getUsedInternalResources(state) {
      return state.usedInternalResources;
    },
    getExternalResources(state) {
      return state.externalResources;
    },
    getRussianInternalResources(state) {
      return state.internalRussianResources;
    },
    getEnglishInternalResources(state) {
      return state.internalEnglishResources;
    },
    getFormStatus(state) {
      const hasExternalResources = state.externalResources.length > 0;
      const hasInternalResources =
        !!state.internalRussianResources && !!state.internalEnglishResources;

      const isFilled =
        (!state.usedExternalResources || hasExternalResources) &&
        (!state.usedInternalResources || hasInternalResources);
      const isNotFilled =
        !state.usedExternalResources &&
        !state.usedInternalResources &&
        !hasInternalResources &&
        !hasExternalResources;

      return getFormStatus({ isFilled, isNotFilled });
    },
    getDataFromForm(state) {
      return {
        UF_CRM_6_EXTERNAL_FUNDING_SOURCES: state.externalResources.map((x) => ({
          ID: x.id,
          NAZVANIE_PROEKTA: x.russianName,
          TITLE: x.englishName,
          DATE_OF_THE_CONTRACT: x.date,
          NUMBER_OF_THE_CONTACT: x.contractNumber,
          NUMBER_OF_THE_PROJECT: x.projectNumber,
          POLNOE_NAIMENOVANIE_FINANSIRUYUSHCHEGO_LITSA: x.russianFullName,
          FULL_NAME_OF_THE_FUNDING_PERSON: x.englishFullName,
        })),
        UF_CRM_6_ISPOLZOVANNIE_RESURSI_INSTITUTA:
          state.internalRussianResources,
        UF_CRM_6_INSTITUTES_RESOURCES_USED_WHILE_CREATING:
          state.internalEnglishResources,
        UF_CRM_6_USE_OF_EXTERNAL_SOURCES: state.usedExternalResources,
        UF_CRM_6_USE_OF_INSTITUTES_RESOURCES: state.usedInternalResources,
      };
    },
  },
  mutations: {
    changeUsedExternalResources(state, value) {
      state.usedExternalResources = value;
    },
    changeUsedInternalResources(state, value) {
      state.usedInternalResources = value;
    },
    addExternalResource(state, resource) {
      const list = state.externalResources.slice();

      list.push(resource);

      state.externalResources = list;
    },
    removeExternalResource(state, index) {
      const list = state.externalResources.slice();

      list.splice(index, 1);

      state.externalResources = list;
    },
    updateExternalResource(state, { index, resource }) {
      const list = state.externalResources.slice();

      list.splice(index, 1, resource);

      state.externalResources = list;
    },
    changeRussianInternalResource(state, value) {
      state.internalRussianResources = value;
    },
    changeEnglishInternalResource(state, value) {
      state.internalEnglishResources = value;
    },
    setDataToForm(state, data) {
      const externalResources = data["UF_CRM_6_EXTERNAL_FUNDING_SOURCES"] || [];

      state.externalResources = externalResources.map((x) => ({
        id: x["ID"],
        russianName: x["NAZVANIE_PROEKTA"] || "",
        englishName: x["TITLE"] || "",
        date: x["DATE_OF_THE_CONTRACT"],
        contractNumber: x["NUMBER_OF_THE_CONTACT"] || "",
        projectNumber: x["NUMBER_OF_THE_PROJECT"] || "",
        russianFullName:
          x["POLNOE_NAIMENOVANIE_FINANSIRUYUSHCHEGO_LITSA"] || "",
        englishFullName: x["FULL_NAME_OF_THE_FUNDING_PERSON"] || "",
      }));
      state.internalRussianResources =
        data["UF_CRM_6_ISPOLZOVANNIE_RESURSI_INSTITUTA"] || "";
      state.internalEnglishResources =
        data["UF_CRM_6_INSTITUTES_RESOURCES_USED_WHILE_CREATING"] || "";
      state.usedExternalResources = !!data["UF_CRM_6_USE_OF_EXTERNAL_SOURCES"];
      state.usedInternalResources =
        !!data["UF_CRM_6_USE_OF_INSTITUTES_RESOURCES"];
    },
  },
};
