<template>
  <AppContainer class="pt-7">
    <div class="d-flex align-items-center mb-6">
      <div class="d-flex header-cell" />
      <div class="d-flex flex-column text-center justify-content-center">
        <VTypography font-style="header-2" font-weight="heavy">{{
          localize.authForm.text
        }}</VTypography>
        <VTypography font-style="header-2" font-weight="heavy">{{
          formName
        }}</VTypography>
      </div>
      <div class="d-flex align-self-center justify-content-end header-cell">
        <AppControl />
      </div>
    </div>

    <LoginForm
      :error="error"
      :submitting="submitting"
      class="mb-5"
      @submit="onSubmit"
    />

    <div class="main-page__divider" />

    <LogoTextIcon class="w-auto" />

    <div
      class="d-flex position-fixed align-items-center justify-content-center main-page__loader"
      v-if="loading"
    >
      <VTypography font-weight="heavy" font-style="header-2">
        ...Загрузка</VTypography
      >
    </div>
  </AppContainer>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex";

import LoginForm from "@/components/forms/LoginForm";
import AppControl from "@/components/app/AppControl";
import VTypography from "@/components/ui/Typography";
import AppContainer from "@/components/app/AppContainer";
import LogoTextIcon from "@/components/icons/LogoTextIcon";

export default {
  name: "MainView",
  inheritAttrs: false,
  components: {
    LogoTextIcon,
    AppControl,
    LoginForm,
    VTypography,
    AppContainer,
  },
  methods: {
    ...mapMutations("application", ["setHash", "setId", "setPass"]),
    ...mapActions("formInfo", ["loadForms"]),
    ...mapActions("i18n", ["loadLang"]),
    onSubmit(pass) {
      this.submitting = true;
      this.error = "";

      this.loadForms(pass)
        .then(() => {
          this.setPass(pass);
          this.submitting = false;
          this.$router.replace("/item");
        })
        .catch((e) => {
          this.error = e["error_message"];
          this.submitting = false;
        });
    },
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", { formName: "getName" }),
  },
  data() {
    return {
      error: "",
      loading: false,
      submitting: false,
    };
  },
  mounted() {
    const { id, hash } = this.$route.query;

    if (id && hash) {
      this.setId(id);
      this.setHash(hash);

      this.loading = true;

      this.loadLang().finally(() => {
        this.loading = false;
      });
    }
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.header-cell {
  flex: 1;
}

.main-page__divider {
  height: 2px;
  margin-bottom: 70px;
  background-color: $gray1;
}

.main-page__loader {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $white;
}
</style>
