import { sleep } from "@/utils/SystemUtils";
import { HttpClient } from "@/helpers/HttpClient";
import { getFormStatus, mapAuthors } from "@/helpers/FormsHelpers";

export const authorsModule = {
  namespaced: true,
  state: {
    authors: [],
    otherAuthors: [],
    instituteAuthors: [],
    withOtherAuthors: false,
  },
  getters: {
    getAuthors(state) {
      return state.authors;
    },
    getInstituteAuthors(state) {
      return state.instituteAuthors;
    },
    getAllAuthors(state) {
      return [...state.instituteAuthors, ...state.otherAuthors];
    },
    getOtherAuthors(state) {
      return state.otherAuthors;
    },
    getFormStatus(state) {
      const hasOtherAuthors = state.otherAuthors.length > 0;
      const hasInstituteAuthors = state.instituteAuthors.length > 0;

      const authors = [
        ...state.instituteAuthors,
        ...(state.withOtherAuthors ? state.otherAuthors : []),
      ];

      const contribution = authors.reduce(
        (acc, item) => acc + item.creative,
        0
      );

      const isContributionComplete = contribution === 100;

      const isFilled =
        (!state.withOtherAuthors || hasOtherAuthors) &&
        hasInstituteAuthors &&
        isContributionComplete;
      const isNotFilled =
        !state.withOtherAuthors &&
        !hasOtherAuthors &&
        !hasInstituteAuthors &&
        !isContributionComplete;

      return getFormStatus({ isFilled, isNotFilled });
    },
    getDataFromForm(state) {
      return {
        UF_CRM_6_NON_INSTITUTE_AUTHORS: state.otherAuthors.map((x) => ({
          ID: x.id,
          UF_CRM_6_AUTHORS_CREATIVE_CONTRIBUTION: x.creative,
          TITLE: x.russianName,
          FIRST_NAME_LAST_NAME: x.englishName,
          MESTO_RABOTY: x.russianWorkplace,
          EMPLOYER: x.englishWorkplace,
          PODRAZDELENIE: x.russianDivision,
          SUBDIVISION: x.englishDivision,
          DOLZHNOST: x.russianPosition,
          POSITION: x.englishPosition,
          GRAZHDANSTVO_CITIZENSHIP: x.isCitizen,
          GRAZHDANSTVO: x.russianOtherCitizenship,
          CITIZENSHIP: x.englishOtherCitizenship,
          TEL_PHONE: x.phoneNumber,
          E_MAIL: x.emailAddress,
          DATE_OF_BIRTH: x.birthday,
          ZAREGISTRIROVAN_PO_ADRESU: x.russianRegisterAddress,
          RESIDENCE_ADDRESS: x.englishRegisterAddress,
          POCHTOVYY_ADRES: x.russianPostAddress,
          POSTAL_ADDRESS: x.englishPostAddress,
          INN_TAXPAYER_IDENTIFICATION_NUMBER_INN: x.inn,
          SNILS_PERSONAL_INSURANCE_POLICY_NUMBER_SNILS: x.snils,
          id1: x.passportSeries,
          id2: x.passportNumber,
          id3: x.passportIssueDate,
          id4: x.passportDivisionCode,
          id5: x.russianPassportIssued,
          id6: x.englishPassportIssued,
        })),
        UF_CRM_6_AUTHORS_OF_THE_INSTITUTE: state.instituteAuthors.map((x) => ({
          ID: x.id,
          UF_CRM_6_AUTHORS_CREATIVE_CONTRIBUTION: x.creative,
          TITLE: x.russianName,
          FIRST_NAME_LAST_NAME: x.englishName,
          MESTO_RABOTY: x.russianWorkplace,
          EMPLOYER: x.englishWorkplace,
          PODRAZDELENIE: x.russianDivision,
          SUBDIVISION: x.englishDivision,
          DOLZHNOST: x.russianPosition,
          POSITION: x.englishPosition,
          GRAZHDANSTVO_CITIZENSHIP: x.isCitizen,
          GRAZHDANSTVO: x.russianOtherCitizenship,
          CITIZENSHIP: x.englishOtherCitizenship,
          TEL_PHONE: x.phoneNumber,
          E_MAIL: x.emailAddress,
          DATE_OF_BIRTH: x.birthday,
          ZAREGISTRIROVAN_PO_ADRESU: x.russianRegisterAddress,
          RESIDENCE_ADDRESS: x.englishRegisterAddress,
          POCHTOVYY_ADRES: x.russianPostAddress,
          POSTAL_ADDRESS: x.englishPostAddress,
          INN_TAXPAYER_IDENTIFICATION_NUMBER_INN: x.inn,
          SNILS_PERSONAL_INSURANCE_POLICY_NUMBER_SNILS: x.snils,
          id1: x.passportSeries,
          id2: x.passportNumber,
          id3: x.passportIssueDate,
          id4: x.passportDivisionCode,
          id5: x.russianPassportIssued,
          id6: x.englishPassportIssued,
        })),
      };
    },
  },
  mutations: {
    updateCreative(state, { id, value }) {
      const otherAuthorsIndex = state.otherAuthors.findIndex(
        (x) => x.id === id
      );
      const instituteAuthorsIndex = state.instituteAuthors.findIndex(
        (x) => x.id === id
      );

      if (otherAuthorsIndex >= 0) {
        const list = state.otherAuthors.slice();

        list.splice(otherAuthorsIndex, 1, {
          ...state.otherAuthors[otherAuthorsIndex],
          creative: value ? Number(value) : 0,
        });

        state.otherAuthors = list;
      } else if (instituteAuthorsIndex >= 0) {
        const list = state.instituteAuthors.slice();

        list.splice(instituteAuthorsIndex, 1, {
          ...state.instituteAuthors[instituteAuthorsIndex],
          creative: value ? Number(value) : 0,
        });

        state.instituteAuthors = list;
      }
    },
    addInstituteAuthor(state, author) {
      const list = state.instituteAuthors.slice();

      list.push(author);

      state.instituteAuthors = list;
    },
    removeInstituteAuthor(state, index) {
      const list = state.instituteAuthors.slice();

      list.splice(index, 1);

      state.instituteAuthors = list;
    },
    addOtherAuthor(state, author) {
      const list = state.otherAuthors.slice();

      list.push(author);

      state.otherAuthors = list;
    },
    removeOtherAuthor(state, index) {
      const list = state.otherAuthors.slice();

      list.splice(index, 1);

      state.otherAuthors = list;
    },
    changeWithOtherAuthors(state, value) {
      state.withOtherAuthors = value;
    },
    updateInstituteAuthor(state, { index, author }) {
      const list = state.instituteAuthors.slice();

      list.splice(index, 1, author);

      state.instituteAuthors = list;
    },
    updateOtherAuthor(state, { index, author }) {
      const list = state.otherAuthors.slice();

      list.splice(index, 1, author);

      state.otherAuthors = list;
    },
    addAuthors(state, list) {
      state.authors = list;
    },
    setDataToForm(state, data) {
      const otherAuthors = data["UF_CRM_6_NON_INSTITUTE_AUTHORS"] || [];
      const instituteAuthors = data["UF_CRM_6_AUTHORS_OF_THE_INSTITUTE"] || [];

      state.otherAuthors = otherAuthors.map((x) => mapAuthors(x));
      state.withOtherAuthors = otherAuthors.length > 0;
      state.instituteAuthors = instituteAuthors.map((x) => mapAuthors(x));
    },
  },
  actions: {
    async getAuthors(store) {
      const httpClient = new HttpClient({});

      try {
        const list = await httpClient.get("mock/authors.json");

        // Имитация запроса на сервер
        await sleep(2000);

        store.commit(
          "addAuthors",
          list.map((x) => mapAuthors(x))
        );
      } catch (e) {
        return e;
      }
    },
  },
};
