import { getFormStatus } from "@/helpers/FormsHelpers";

export const generalModule = {
  namespaced: true,
  state: {
    email: "",
    russianTechnology: "",
    englishTechnology: "",
    russianInformation: "",
    englishInformation: "",
  },
  getters: {
    getRussianInformation(state) {
      return state.russianInformation;
    },
    getEnglishInformation(state) {
      return state.englishInformation;
    },
    getEmail(state) {
      return state.email;
    },
    getRussianTechnology(state) {
      return state.russianTechnology;
    },
    getEnglishTechnology(state) {
      return state.englishTechnology;
    },
    getFormStatus(state) {
      const hasEmail = !!state.email;
      const hasRussian = !!state.russianInformation;
      const hasEnglish = !!state.englishInformation;

      const hasRussianTechnology = !!state.russianTechnology;
      const hasEnglishTechnology = !!state.englishTechnology;

      const isFilled =
        hasEmail &&
        hasRussian &&
        hasEnglish &&
        hasEnglishTechnology &&
        hasRussianTechnology;
      const isNotFilled =
        !hasEmail &&
        !hasRussian &&
        !hasEnglish &&
        !hasEnglishTechnology &&
        !hasRussianTechnology;

      return getFormStatus({ isFilled, isNotFilled });
    },
    getDataFromForm(state) {
      return {
        UF_CRM_6_EMAIL: state.email || "",
        UF_CRM_6_POLNOYE_NAIMENOVANIE: state.russianTechnology || "",
        UF_CRM_6_FULL_TITLE: state.englishTechnology || "",
        UF_CRM_6_NAPRAVLENO: state.russianInformation || "",
        UF_CRM_6_SUBMITTED_BY: state.englishInformation || "",
      };
    },
  },
  mutations: {
    changeRussianInformation(state, value) {
      state.russianInformation = value;
    },
    changeEnglishInformation(state, value) {
      state.englishInformation = value;
    },
    changeEmail(state, value) {
      state.email = value;
    },
    changeRussianTechnology(state, value) {
      state.russianTechnology = value;
    },
    changeEnglishTechnology(state, value) {
      state.englishTechnology = value;
    },
    setDataToForm(state, data) {
      state.email = data["UF_CRM_6_EMAIL"];
      state.russianTechnology = data["UF_CRM_6_POLNOYE_NAIMENOVANIE"];
      state.englishTechnology = data["UF_CRM_6_FULL_TITLE"];
      state.russianInformation = data["UF_CRM_6_NAPRAVLENO"];
      state.englishInformation = data["UF_CRM_6_SUBMITTED_BY"];
    },
  },
};
