<template>
  <VButton
    :class="rootClasses"
    class="action-button"
    @mouseenter="hover = true"
    @mouseleave="hover = false"
    :disabled="disabled || loading"
  >
    <div v-if="hasLeftIcon" class="me-1">
      <slot name="leftIcon" :hover="hover"></slot>
    </div>

    <div class="action-button__title-container">
      <VTypography
        font-weight="heavy"
        :class="titleClasses"
        class="action-button__title"
      >
        <slot></slot>
      </VTypography>

      <div v-if="loading" class="action-button__loader-container">
        <VLoader />
      </div>
    </div>

    <div v-if="hasRightIcon" class="ms-1">
      <slot name="rightIcon" :hover="hover"></slot>
    </div>
  </VButton>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";
import VLoader from "@/components/ui/Loader";

export default {
  name: "ActionButton",
  components: { VLoader, VTypography, VButton },
  props: {
    disabled: Boolean,
    loading: Boolean,
    buttonType: {
      type: String,
      default: "primary",
      validator(value) {
        return [
          "primary",
          "secondary",
          "outline",
          "secondary-outline",
        ].includes(value);
      },
    },
  },
  data() {
    return {
      hover: false,
    };
  },
  computed: {
    hasRightIcon() {
      return !!this.$slots.rightIcon;
    },
    hasLeftIcon() {
      return !!this.$slots.leftIcon;
    },
    rootClasses() {
      return {
        "action-button--hover": this.hover || this.buttonType === "secondary",
        "action-button--outline": this.hover || this.buttonType === "outline",
        "action-button--secondary-outline":
          this.buttonType === "secondary-outline",
        "action-button--secondary-outline-disabled":
          this.disabled && this.buttonType === "secondary-outline",
        "action-button--secondary-outline-hover":
          this.hover && this.buttonType === "secondary-outline",
      };
    },
    titleClasses() {
      return {
        "action-button__title--hidden": this.loading,
        "action-button__title--hover":
          this.hover || this.buttonType === "secondary",
        "action-button__title--outline":
          this.hover || this.buttonType === "outline",
        "action-button__title--secondary-outline":
          this.buttonType === "secondary-outline",
        "action-button__title--secondary-outline-disabled":
          this.disabled && this.buttonType === "secondary-outline",
      };
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.action-button {
  min-height: 45px;
  max-height: 45px;
  background-color: $gray1;
}

.action-button--outline {
  border: 2px solid $green;
}

.action-button--hover {
  background-color: $green;
}

.action-button__title {
  color: $white;
}

.action-button__title--outline {
  color: $green;
}

.action-button__title--hover {
  color: $gray1;
}

.action-button__title-container {
  position: relative;
}

.action-button__loader-container {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
}

.action-button__title--hidden {
  opacity: 0;
}

.action-button--secondary-outline {
  background-color: $green;
}

.action-button--secondary-outline-hover {
  background-color: transparent;
}

.action-button__title--secondary-outline {
  color: $gray1;
}

.action-button--secondary-outline-disabled {
  cursor: default;
  background-color: $gray5;
}

.action-button__title--secondary-outline-disabled {
  color: $gray4;
}
</style>
