<template>
  <div class="base-input" :class="rootClass">
    <textarea
      rows="1"
      ref="input"
      v-bind="$attrs"
      v-if="$attrs.multiline"
      class="w-100 base-input__field"
    />
    <input
      ref="input"
      v-bind="$attrs"
      @keydown="onKeyDown"
      v-if="!$attrs.multiline"
      class="base-input__field"
    />
  </div>
</template>

<script>
import autosize from "@github/textarea-autosize";

export default {
  name: "BaseInput",
  inheritAttrs: false,
  props: {
    rootClass: String,
  },
  data() {
    return {
      autosizeInstance: {},
    };
  },
  beforeUnmount() {
    if (this.$attrs.multiline) {
      this.autosizeInstance.unsubscribe();
    }
  },
  mounted() {
    if (this.$attrs.multiline) {
      this.autosizeInstance = autosize(this.$refs.input);
    }
  },
  methods: {
    onKeyDown(event) {
      if (this.$attrs?.type === "number") {
        const value = event.target.value;
        const isBackspace = event.code === "Backspace";
        const isDigit = event.code.indexOf("Digit") === 0;

        if (
          (!isDigit ||
            (this.$attrs.maxlength &&
              value.length === this.$attrs.maxlength)) &&
          !isBackspace
        ) {
          event.preventDefault();
        }
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.base-input {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.base-input__field {
  resize: none;
  border: none;
  outline: none;
  padding: 12px 0;
  background-color: transparent;
  font-weight: $font-weight-medium;
}
</style>
