import { DateTime } from "luxon";
import { sleep } from "@/utils/SystemUtils";
import {
  canEditForm,
  hasEditForms,
  isEditableStatus,
  isRevisionForm,
  isRevisionFormIncludes,
  isRevisionStatus,
} from "@/helpers/StatusHelpers";
import { HttpClient } from "@/helpers/HttpClient";
import { FormStatus, FormTypes } from "@/constants/FormConstants";

export const formInfoModule = {
  namespaced: true,
  state: {
    name: "",
    revisionForms: [],
    savedAt: undefined,
    type: FormTypes.Tech,
    status: FormStatus.Draft,
  },
  getters: {
    getStatus(state) {
      return state.status;
    },
    getRevisionForms(state) {
      return state.revisionForms;
    },
    hasEditForms(state) {
      return hasEditForms(state.status, state.revisionForms);
    },
    getFormStatus(state) {
      return (formKey) => {
        if (isRevisionForm(formKey, state.status, state.revisionForms)) {
          return FormStatus.Revision;
        }

        if (
          isRevisionStatus(state.status) &&
          !isRevisionFormIncludes(formKey, state.revisionForms)
        ) {
          return FormStatus.Signed;
        }

        return state.status;
      };
    },
    canEditForm(state) {
      return (formKey) =>
        canEditForm(formKey, state.status, state.revisionForms);
    },
    getSavedAt(state) {
      return state.savedAt;
    },
    getName(state) {
      return state.name;
    },
    getFormType(state) {
      return state.type;
    },
    isEditableStatus(state) {
      return isEditableStatus(state.status);
    },
    getType(state) {
      return state.type;
    },
  },
  mutations: {
    changeFormType(state, type) {
      state.type = type;
    },
    changeSavedAt(state, value) {
      state.savedAt = value;
    },
    setDataToForm(state, data) {
      state.name = data.name;
      state.status = data["STATUS"];
      state.revisionForms = data["REVISION_FORMS"];
      state.type = data["UF_CRM_6_FORM_TYPE"] || FormTypes.Tech;
      state.savedAt = data["UF_CRM_6_DATE_OF_RECEIPT"];
    },
  },
  actions: {
    async saveForms(store) {
      // Имитация запроса на сервер
      await sleep();

      store.commit("changeSavedAt", DateTime.now().toISO());
    },
    async loadForms(store, pass) {
      const httpClient = new HttpClient({});

      const id = store.rootGetters["application/getId"];
      const hash = store.rootGetters["application/getHash"];

      const { data } = await httpClient.get(
        `https://my.skoltech.ru/api/fdfia/getFields?id=${id}&hash=${hash}&pass=${pass}`
      );

      if (data) {
        store.commit("dates/setDataToForm", data, { root: true });
        store.commit("general/setDataToForm", data, { root: true });
        store.commit("authors/setDataToForm", data, { root: true });
        store.commit("formInfo/setDataToForm", data, { root: true });
        store.commit("resources/setDataToForm", data, { root: true });
        store.commit("disclosure/setDataToForm", data, { root: true });
        store.commit("technology/setDataToForm", data, { root: true });
        store.commit("organizations/setDataToForm", data, { root: true });
      }
    },
    async sendForms(store) {
      try {
        const dates = store.rootGetters["dates/getDataFromForm"];
        const general = store.rootGetters["general/getDataFromForm"];
        const authors = store.rootGetters["authors/getDataFromForm"];
        const formInfo = store.rootGetters["formInfo/getDataFromForm"];
        const resources = store.rootGetters["resources/getDataFromForm"];
        const technology = store.rootGetters["technology/getDataFromForm"];
        const disclosure = store.rootGetters["disclosure/getDataFromForm"];
        const description = store.rootGetters["description/getDataFromForm"];
        const organizations =
          store.rootGetters["organizations/getDataFromForm"];

        const data = {
          ...dates,
          ...general,
          ...authors,
          ...formInfo,
          ...resources,
          ...technology,
          ...disclosure,
          ...description,
          ...organizations,
        };

        const httpClient = new HttpClient({});

        const id = store.rootGetters["application/getId"];
        const hash = store.rootGetters["application/getHash"];
        const pass = store.rootGetters["application/getPass"];

        await httpClient.post("https://my.skoltech.ru/api/fdfia/save", {
          body: JSON.stringify({
            id,
            hash,
            pass,
            data,
          }),
        });
      } catch (e) {
        // error
      }
    },
  },
};
