<template>
  <div class="d-flex flex-fill flex-column align-self-center login-form">
    <PasswordInput
      :maxlength="8"
      :multiline="false"
      v-model="password"
      :error="error"
      root-class="mb-1 login-form__password-input"
    />

    <ActionButton :loading="submitting" class="mb-4 mt-1" @click="onSubmit">{{
      localize.authFormLogIn.text
    }}</ActionButton>

    <VTypography
      font-style="text-2"
      font-weight="medium"
      class="text-center login-form__info-text"
      v-html="footerText"
    />
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import PasswordInput from "@/components/ui/PasswordInput";
import ActionButton from "@/components/buttons/ActionButton";
import { mapGetters } from "vuex";
import { formatString } from "@/utils/FormatUtils";

export default {
  name: "LoginForm",
  components: {
    VTypography,
    ActionButton,
    PasswordInput,
  },
  props: {
    error: String,
    submitting: Boolean,
  },
  data() {
    return {
      password: "",
    };
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    footerText() {
      return formatString(
        this.localize.authFormFootnote.text,
        this.localize.authFormFootnote.params
      );
    },
  },
  emits: {
    submit: () => true,
  },
  methods: {
    onSubmit() {
      this.$emit("submit", this.password);
    },
  },
};
</script>

<style lang="scss">
@import "~@/assets/styles/variables.scss";

.login-form {
  max-width: 335px;
}

.login-form__password-input {
  width: 335px;
}

.login-form__info-text {
  color: $gray4;

  & > .app-link {
    color: $gray4;
  }
}
</style>
