<template>
  <div class="d-flex flex-column" :class="rootClass">
    <TypographyWithInfo
      class="mb-1"
      v-if="!!label"
      font-style="text-1"
      font-weight="medium"
      :class="labelClasses"
      :type="inputType"
      :popover-position="popoverPosition"
    >
      <template v-slot:default>
        {{ label }}
      </template>

      <template v-if="hasPopover" v-slot:popover>
        <slot name="popover"></slot>
      </template>
    </TypographyWithInfo>

    <div
      :class="containerClasses"
      @mouseenter="hover = true"
      @mouseleave="hover = false"
      class="d-flex flex-column text-input__container"
    >
      <div v-if="hasTopAdornment" class="mt-1 ms-2">
        <slot name="topAdornment" />
      </div>

      <div class="d-flex">
        <slot name="startAdornment" v-if="hasStartAdornment" />

        <BaseInput
          ref="text-input"
          v-bind="$attrs"
          autocomplete="off"
          :value="modelValue"
          @blur="focus = false"
          @focus="focus = true"
          @click="onFieldClick"
          :class="fieldClasses"
          :multiline="multiline"
          :maxlength="maxlength"
          class="w-100 text-input__field"
          root-class="p-0 flex-grow-1 flex-shrink-1 mx-2"
          @input="$emit('update:modelValue', $event.target.value)"
        />

        <div
          v-if="hasEndAdornment"
          class="d-flex me-2 justify-content-center align-items-center"
        >
          <slot name="endAdornment" />
        </div>

        <TextCounter
          class="mt-2 me-2"
          :text="modelValue"
          :class="counterClasses"
          :max-count="maxlength"
          v-if="
            showCounter &&
            !!maxlength &&
            !$attrs.disabled &&
            (!!modelValue || focus)
          "
        />
      </div>
    </div>

    <VTypography font-style="text-2" v-if="!!error" class="text-input__error">{{
      error
    }}</VTypography>
  </div>
</template>

<script>
import TextCounter from "@/components/ui/TextCounter";
import VTypography from "@/components/ui/Typography";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import BaseInput from "@/components/ui/BaseInput";

export default {
  name: "TextInput",
  inheritAttrs: false,
  components: {
    BaseInput,
    VTypography,
    TextCounter,
    TypographyWithInfo,
  },
  props: {
    showCounter: {
      type: Boolean,
      default: true,
    },
    popoverPosition: String,
    label: String,
    error: String,
    rootClass: String,
    modelValue: String,
    multiline: {
      type: Boolean,
      default: true,
    },
    maxlength: {
      type: Number,
      default: 100,
    },
    inputType: {
      type: String,
      default: "default",
      validator(value) {
        return ["default", "contrast"].includes(value);
      },
    },
  },
  data() {
    return {
      hover: false,
      focus: false,
    };
  },
  computed: {
    hasTopAdornment() {
      return !!this.$slots.topAdornment;
    },
    hasEndAdornment() {
      return !!this.$slots.endAdornment;
    },
    hasStartAdornment() {
      return !!this.$slots.startAdornment;
    },
    hasPopover() {
      return !!this.$slots.popover;
    },
    containerClasses() {
      return {
        "text-input__container--focus": this.focus,
        "text-input__container--error": !!this.error,
        "text-input__container--disabled": !!this.$attrs.disabled,
        "border-white": !this.error && this.inputType === "contrast",
        "text-input__container--hover":
          ((!this.focus && this.hover) || (!this.focus && this.modelValue)) &&
          !this.$attrs.disabled,
      };
    },
    fieldClasses() {
      return {
        "text-white": this.inputType === "contrast",
      };
    },
    labelClasses() {
      return {
        "text-white": this.inputType === "contrast",
      };
    },
    counterClasses() {
      return {
        "text-input__counter--contract": this.inputType === "contrast",
      };
    },
  },
  methods: {
    onFieldClick(event) {
      event.stopPropagation();
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.text-input__container {
  @include transition(border-color);

  min-height: 45px;
  border: 2px solid $gray4;
}

.text-input__container--hover {
  border-color: $gray1;
}

.text-input__container--focus {
  border-color: $green;
}

.text-input__container--error {
  border-color: $red;
  margin-bottom: 5px;
}

.text-input__container--disabled {
  border-color: $gray5;
  background-color: $gray5;
}

.text-input__counter--contract {
  color: $gray2;
}

.text-input__error {
  color: $red;
}
</style>
