import { FormStatus } from "@/constants/FormConstants";

export function formatStatusTitle(status) {
  switch (status) {
    case "inspection":
      return "commonFormInspection";
    case "revision":
      return "commonFormRevision";
    case "accepted":
      return "commonFormAccepted";
    case "signed":
      return "commonFormSignet";
    default:
      return "commonFormDraft";
  }
}

export function isDraftStatus(status) {
  return status === FormStatus.Draft;
}

export function isRevisionStatus(status) {
  return status === FormStatus.Revision;
}

export function isEditableStatus(status) {
  return isDraftStatus(status);
}

export function isRevisionFormIncludes(formKey, revisionForms = []) {
  return revisionForms.includes(formKey);
}

export function isRevisionForm(formKey, status, revisionForms = []) {
  return (
    isRevisionStatus(status) && isRevisionFormIncludes(formKey, revisionForms)
  );
}

export function canEditForm(formKey, status, revisionForms = []) {
  return (
    isEditableStatus(status) || isRevisionForm(formKey, status, revisionForms)
  );
}

export function hasEditForms(status, revisionForms = []) {
  return (
    isEditableStatus(status) ||
    (isRevisionStatus(status) && revisionForms.length > 0)
  );
}
