export function mapAuthors(data) {
  return {
    id: data["ID"],
    creative: data["UF_CRM_6_AUTHORS_CREATIVE_CONTRIBUTION"] || "",
    russianName: data["TITLE"] || "",
    englishName: data["FIRST_NAME_LAST_NAME"] || "",
    russianWorkplace: data["MESTO_RABOTY"] || "",
    englishWorkplace: data["EMPLOYER"] || "",
    russianDivision: data["PODRAZDELENIE"] || "",
    englishDivision: data["SUBDIVISION"] || "",
    russianPosition: data["DOLZHNOST"] || "",
    englishPosition: data["POSITION"] || "",
    isCitizen: !!data["GRAZHDANSTVO_CITIZENSHIP"],
    russianOtherCitizenship: data["GRAZHDANSTVO"] || "",
    englishOtherCitizenship: data["CITIZENSHIP"] || "",
    phoneNumber: data["TEL_PHONE"] || "",
    emailAddress: data["E_MAIL"] || "",
    birthday: data["DATE_OF_BIRTH"],
    russianRegisterAddress: data["ZAREGISTRIROVAN_PO_ADRESU"] || "",
    englishRegisterAddress: data["RESIDENCE_ADDRESS"] || "",
    russianPostAddress: data["POCHTOVYY_ADRES"] || "",
    englishPostAddress: data["POSTAL_ADDRESS"] || "",
    inn: data["INN_TAXPAYER_IDENTIFICATION_NUMBER_INN"] || "",
    snils: data["SNILS_PERSONAL_INSURANCE_POLICY_NUMBER_SNILS"] || "",
    passportSeries: data["SERIYA_SERIES"] || "",
    passportNumber: data["NOMER_NUMBER"] || "",
    passportIssueDate: data["ISSUED_BY"] || "",
    passportDivisionCode: data["KOD_PODRAZDELENIYA_DIVISION_CODE"] || "",
    russianPassportIssued: data["KEM_VYDAN"] || "",
    englishPassportIssued: data["ISSUED_BY"] || "",
  };
}
