import { getFormStatus } from "@/helpers/FormsHelpers";

export const organizationsModule = {
  namespaced: true,
  state: {
    organizations: [],
  },
  getters: {
    getOrganizations(state) {
      return state.organizations;
    },
    getFormStatus(state) {
      const hasOrganizations = !!state.organizations.length > 0;

      const isFilled = hasOrganizations;
      const isNotFilled = !hasOrganizations;

      return getFormStatus({ isFilled, isNotFilled });
    },
    getDataFromForm(state) {
      return {
        UF_CRM_6_INSTEAD_COMMERCIAL_ENTITIES: state.organizations.map((x) => ({
          ID: x.id,
          POLNOE_NAIMENOVANIE: x.russianName,
          TITLE: x.englishName,
          KONTAKTY: x.russianContact,
          CONTACTS: x.englishContact,
        })),
      };
    },
  },
  mutations: {
    addOrganization(state, organization) {
      const list = state.organizations.slice();

      list.push(organization);

      state.organizations = list;
    },
    updateOrganization(state, { index, organization }) {
      const list = state.organizations.slice();

      list.splice(index, 1, organization);

      state.organizations = list;
    },
    removeOrganization(state, index) {
      const list = state.organizations.slice();

      list.splice(index, 1);

      state.organizations = list;
    },
    setDataToForm(state, data) {
      const organizations = data["UF_CRM_6_INSTEAD_COMMERCIAL_ENTITIES"] || [];

      state.organizations = organizations.map((x) => ({
        id: x["ID"],
        russianName: x["POLNOE_NAIMENOVANIE"] || "",
        englishName: x["TITLE"] || "",
        russianContact: x["KONTAKTY"] || "",
        englishContact: x["CONTACTS"] || "",
      }));
    },
  },
};
